.App-logo {
  height: 100px;
}

.main-calculator {
  padding: 1.5rem 0;
}

.calculator-header {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.main-calculator form {
  padding: 2rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.main-calculator form div {
  display: flex;
  flex-direction: row;
  justify-content: center;
  flex-wrap: wrap;
  margin: 5px;
}

.main-calculator input,
.calculator-button {
  font-size: 20px;
  padding: 10px;
  border-radius: 5px;
}

.main-calculator input {
  display: block;
  margin-bottom: 20px;
  width: 35.5%;
}

.calculator-button {
  border: 1px solid gray;
  background: whitesmoke;
  margin: 5px;
}

.calculator-button:nth-last-child(2),
.calculator-button:nth-last-child(1) {
  background: black;
  color: white;
}

form p {
  width: auto;
  background-color: black;
  padding: 15px 20px;
  color: white;
  border: 1px solid white;
  border-radius: 5px;
  margin: 5px;
}