:root {
    --bg-color: #4b4533;
    --typing-text-color: #ffd868;
    --accent-color: #ffd868;
    --font-family: sans-serif;
    --border-radius: 5px;
    --font-size-small: 15px;
    --font-size-medium: 20px;
    --font-size-large: 35px;
    --input-bg-color: #fffbf0;
}

.typing-test-div {
    margin: 0 50px;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 20px;
    font-family: sans-serif;
}

.typing-test-div .clock {
    margin-bottom: 100px;
    background: #282820;
}

.typing-test-div .date {
    font-size: var(--font-size-small);
    font-weight: bold;
    color: var(--typing-text-color);
}

.typing-test-div .time {
    background: var(--accent-color);
    box-shadow: 0 0 30px var(--accent-color);
    color: #272727;
    font-size: var(--font-size-large);
    border: 3px solid var(--accent-color);
    border-radius: var(--border-radius);
    padding: 10px;
}

.typing-test-div .counter-timer-buttons {
    display: flex;
    gap: 10px;
    align-items: flex-end;
}

.typing-test-div .counter-timer-buttons span,
.typing-test-div .time-left,
.speed-details,
.typing-test-div .reset-button {
    color: var(--typing-text-color);
}

.typing-test-div .counter-timer-buttons span {
    font-size: var(--font-size-medium);
}

.typing-test-div .counter-timer-buttons button,
.typing-test-div .reset-button {
    background: var(--bg-color);
    border: 1px solid var(--accent-color);
    font-size: 18px;
    border-radius: var(--border-radius);
    padding: 5px 15px;
    color: var(--accent-color);
}

.typing-test-div .reset-button {
    padding: 8px 20px;
}

.typing-test-div .counter-timer-buttons button:hover,
.typing-test-div .reset-button:hover {
    background: var(--accent-color);
    color: var(--bg-color);
}

.typing-test-div .time-left,
.speed-details {
    display: flex;
    gap: 20px;
    font-size: var(--font-size-medium);
    font-weight: bold;
    margin: 20px 0;
}

.typing-words {
    font-size: 25px;
    padding: 5px;
    border-radius: var(--border-radius);
}

.active {
    background-color: #d3b96d;
}

.correct {
    color: green;
}

.incorrect {
    color: red;
}

.typing-test-div .text-and-field {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 20px;
}

.typing-test-div .reference-text,
.typing-test-div .input-field {
    background: var(--input-bg-color);
    border: 2px solid var(--typing-text-color);
    border-radius: var(--border-radius);
    width: 70%;
    padding: 10px;
}

.typing-test-div .reference-text {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    gap: 3px;
    width: 85%;
}

.typing-test-div .input-field {
    outline: none;
    font-size: 20px;
    width: 85%;
}

@media only screen and (max-width: 1200px) {

    .typing-test-div .input-field,
    .typing-test-div .reference-text {
        width: 100%;
    }
}