:root {
  --box-shadow: 0 10px 20px rgba(0,0,0,0.2);
}

[data-theme="dark"] {
  --box-shadow: 0 10px 20px rgba(161, 154, 154, 0.288);
}

.card{
    display: flex;
    flex-direction: column;
    width: 45%;
    justify-content: flex-start;
    align-items: center;
    background: #6264fb50;
    border-radius: 5%;
    margin: 20px 0;
    padding: 10px;
    color: white;
    box-shadow: var(--box-shadow);
    backdrop-filter: blur(50px);
    border: 1px solid #aca9a9;
}
.card img{
    border-radius: 5%;
}
.card a{
text-decoration: none;
}

@media only screen and (max-width:900px) {
    .card{
        width: 100%;
    }
}

.button-64 {
    align-items: center;
    background-image: linear-gradient(144deg,#AF40FF, #5B42F3 50%,#00DDEB);
    border: 0;
    border-radius: 8px;
    box-shadow: rgba(151, 65, 252, 0.2) 0 15px 30px -5px;
    box-sizing: border-box;
    color: #FFFFFF;
    display: flex;
    font-family: Phantomsans, sans-serif;
    font-size: 20px;
    justify-content: center;
    line-height: 1em;
    max-width: 100%;
    min-width: 140px;
    padding: 3px;
    text-decoration: none;
    user-select: none;
    -webkit-user-select: none;
    touch-action: manipulation;
    white-space: nowrap;
    cursor: pointer;
  }
  
  .button-64:active,
  .button-64:hover {
    outline: 0;
  }
  
  .button-64 span {
    background-color: rgb(5, 6, 45);
    padding: 16px 24px;
    border-radius: 6px;
    width: 100%;
    transition: 300ms;
  }
  
  .button-64:hover span {
    background: none;
  }
  
  @media (min-width: 768px) {
    .button-64 {
      font-size: 24px;
      min-width: 196px;
    }
  }

  





