  .calculator {
    background-color: #17181a;
    padding: 25px;
    border-radius: 15px;
    box-shadow:rgb(0, 0, 0) 30px 44px 100px;
  }
  
  .calculator-display {
    background-color: #3e4656;
    width: 300px;
    border-radius: 10px;
    padding: 10px;
    margin-bottom: 20px;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
  }
  
  .calculator-expression {
    font-size: 20px;
    color: #1991ff;
    height: 40px;
  }
  
  .calculator-result {
    font-size: 30px;
    color: whitesmoke;
    height: 50px;
  }
  
  .calculator .calculator-buttons {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 15px;
  }

  .calculator-buttons button {
    background-color: #303136;
    border: none;
    border-radius: 5px;
    padding: 19px 10px;
    font-size: 24px;
    color: #1991ff;
    cursor: pointer;
    box-shadow:  0px 2px #4e4c4c;
  }
  .calculator-buttons button:nth-child(1), 
  .calculator-buttons button:nth-child(2) {
      background-color: #878888;
      color: whitesmoke;
  }
  
  .calculator-buttons button:nth-child(3), 
  .calculator-buttons button:nth-child(4) {
      background-color: #606161;
      color: whitesmoke;
  }
  .calculator-buttons button:nth-child(1):hover, 
  .calculator-buttons button:nth-child(2):hover,
  .calculator-buttons button:nth-child(3):hover, 
  .calculator-buttons button:nth-child(4):hover {
      background-color: darkgray;
  }
  .calculator-buttons button:nth-child(5),
  .calculator-buttons button:nth-child(6),
  .calculator-buttons button:nth-child(7),
  .calculator-buttons button:nth-child(8),
  .calculator-buttons button:nth-child(12),
  .calculator-buttons button:nth-child(16) {
      background-color: #005db2;
      color: whitesmoke;
      box-shadow:  0px 2px #4d7fad;
  }
  
  .calculator-buttons button:nth-child(20) {
      background-color: #1991ff;
      color: whitesmoke;
  }
  
  .calculator-buttons button:nth-child(5):hover,
.calculator-buttons button:nth-child(6):hover,
.calculator-buttons button:nth-child(7):hover,
.calculator-buttons button:nth-child(8):hover,
.calculator-buttons button:nth-child(12):hover,
.calculator-buttons button:nth-child(16):hover,
.calculator-buttons button:nth-child(20):hover {
  background-color: #1991ff;
  
}

  .calculator-buttons button:hover {
    background-color: rgb(109, 108, 108);
  }
  
  .enter {
    margin-top: 38px;
    margin-bottom: -80px;
    padding: 0 !important;
  }
  .enter:hover {
    background-color: #4169E1;
  }
  .plus{
    margin-top: 0px;
    margin-bottom: -38px;
    padding: 0 !important;
  }
 
  
  
  @media only screen and (max-width:450px) {
      .calculator{
        padding: 15px;
      }
      .calculator-buttons{
        gap: 10px;
      }
      .calculator-buttons button{
        padding: 15px 5px;
      }
  }