:root {
  --background: url("../assets/back.png"); 
  --text-color: #000;
  --nav-item-color: #000;
  --border-color: #000;
  --nav-item-background-hover: #6265fb;
  --nav-item-background-active: #6265fb;
}

[data-theme="dark"] {
  --background: linear-gradient(89deg, rgb(0, 25, 31) 0%, rgba(9, 6, 17, 0.884) 80%); 
  --text-color: #000;
  --text-color: #fff;
  --border-color: #fff;
  --nav-item-color: #fff;
  --nav-item-background-hover: #6265fb;
  --nav-item-background-active: #6265fb;
}


.typing-test {
  display: flex;
  background:  #4b4533 !important;
  align-items: center;
  justify-content: center;
  padding: 0;
}
.calculator-advance{
  background: url("../assets/calculator.jpeg") !important;
    background-size: cover;
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    margin: 0;
}
.todo-list-app{ 
  background: #1051000f !important;
  background-size: cover;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  margin: 0;
padding: 0;}

.body {
  background: var(--background);
  color: var(--text-color);
  min-height: 100vh;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: top;
}

.header {
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid rgba(238, 233, 233, 0.322);
  display: none;
}

.logo {
  display: flex;
  align-content: center;
  align-items: center;
}

.logo .header-name {
  font-family: 'Brush Script MT', cursive;
  font-weight: 500;
  font-size: 1.9rem;
  letter-spacing: 5px;
  color: var(--text-color);
}

.nav {
  display: flex;
  justify-content: center;
  padding: 1rem 1rem;
}

.nav-item {
  text-decoration: none;
  color: var(--nav-item-color);
  padding: 10px 15px;
  margin: 10px 10px;
  border-radius: 10px;
  align-self: center;
  transition: background-color 0.3s ease;
}
.hidden {
  display: none;
}

.nav-item:hover {
  background-color: var(--nav-item-background-hover);
}

nav a.active {
  background-color: var(--nav-item-background-active);
  color: white;
}

/* burger menu settings */
.hr {
  margin: 0;
  background-color: var(--text-color);
  border-color: transparent;
  width: 100%;
}

.mobile-header {
  height: 3.3rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom: 1px solid rgba(238, 233, 233, 0.322);
}

.mobile-header button {
  border: 0;
  background-color: var(--nav-item-background-active);
  border: 1px solid var(--text-color);
  padding: 0px 15px;
  border-radius: 14px;
}

.nav-menu2 {
  padding: 1rem 0;
  margin: 0;
  visibility: hidden;
  transform-origin: left center;
  transform: translateX(-100%);
  transition: visibility 0.5s, transform 0.5s;
  position: absolute;
  top: 102px;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 50;
  background: var(--background);
  background-repeat: no-repeat;
  background-size: cover;
}

.nav-menu2.active {
  background: var(--background);
  background-repeat: no-repeat;
  background-size: cover;
  position: absolute;
  top: 102px;
  z-index: 5;
  width: 100%;
  visibility: visible;
  transform: translateX(0);
}

.mobile-header ul {
  list-style-type: none;
}

.nav-menu2 li {
  padding: 20px 25px;
  display: flex;
  flex-direction: column;
}

@media only screen and (max-width: 900px) {
  .header {
    display: none;
  }
  .mobile-header {
    height: 100px;
  }
  a.active {
    background-color: var(--nav-item-background-active);
  }
  .nav-item {
    color: var(--nav-item-color);
  }
  .hr {
    background-color: var(--text-color);
  }
}

@media only screen and (min-width: 901px) {
  .mobile-header {
    display: none;
  }
  .nav-item {
    color: var(--nav-item-color);
  }
  .hr {
    background-color: var(--text-color);
  }
}

