.intro-section {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 4rem 0;
}

.intro-text {
  width: 50%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.intro-text h1 {
  font-size: 40px;
  margin: 0;
}

.intro-text .highlight {
  color: #6265fb;
}

.intro-text p {
  font-size: 1.2em;
  margin: 10px 0 20px;
  text-align: center;
}

.about-btn {
  display: inline-block;
  padding: 10px 20px;
  background-color: #007bff;
  color: #fff;
  text-decoration: none;
  border: none;
  border-radius: 5px;
  transition: background-color 0.3s ease;
  font-size: 1.2rem;
}

.about-btn:hover {
  background-color: #0056b3;
}

.social-icons {
  margin-top: 20px;
}

.social-icons a {
  margin-right: 10px;
  display: inline-block;
}

.social-icons img {
  width: 70px;
  height: 70px;
  transition: transform 0.3s ease;
}

.social-icons img:hover {
  transform: scale(1.1);
}

.intro-image {
  width: 50%;
  max-width: 50%;
  display: flex;
  justify-content: center;
}


.about-section {
  display: flex;
  align-items: center;
  padding: 4rem 0;
}

.about-image {
  width: 40%;
  display: flex;
  justify-content: center;
}

.about-image .image {
  width: 70%;
  border: 1px solid white;
  box-shadow: #a2a3ff 0px 7px 29px 0px;
}

.about-text {
  width: 50%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.about-left {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: #fff;
}

.about-left-content>div {

  padding: 2rem 2rem 2rem 2rem;
  text-align: center;
  border-radius: 12px 12px 0 0;
}

.about-left-content {
  box-shadow: 0px 0px 18px -1px rgba(0, 0, 0, 0.39);
  -webkit-box-shadow: 0px 0px 18px -1px rgba(0, 0, 0, 0.39);
  -moz-box-shadow: 0px 0px 18px -1px rgba(0, 0, 0, 0.39);
  border-radius: 12px;
  width: 100%;
}

.about-img img {
  display: block;
  width: 250px;
}

.about-img {
  width: 250px;
  height: 250px;
  overflow: hidden;
  border-radius: 50%;
  transition: all 0.5s ease-in-out;
}

.shadow {
  margin-left: auto;
  margin-right: auto;
  border-radius: 50%;
  width: 250px;
  height: 250px;
}

.about-left-content h2 {
  font-size: 2rem;
  margin: 2.2rem 0 0.6rem 0;
  line-height: 1.2;
  padding-bottom: 1rem;
  border-bottom: 2px solid #6265fb;
}

.about-left-content h3 {
  text-transform: uppercase;
  font-weight: 300;
  letter-spacing: 5px;
  margin-top: 1.2rem;
}

.about-right {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  color: #fff;
}

.about-right h1 {
  font-size: 60px;
  text-transform: uppercase;
  color: var(--text-color);
}

.about-right h1 span {
  color: #6265fb;
}

.about-right h2 {
  font-weight: 600;
  color: var(--text-color);
}
.about-btns {
  display: flex;
  margin: 2rem 0;
}

.btn {
  border: none;
  font-size: 0.9rem;
  text-transform: uppercase;
  border: 2px solid #fff;
  border-radius: 20px;
  padding: 0.55rem 0;
  width: 130px;
  font-weight: 600;
  background: transparent;
  margin: 0 0.5rem;
  cursor: pointer;
  color: #fff
}

.btn.btn-pink {
  background: #6265fb;
  color: #fff;
  border-color: #6265fb;
  transition: all 0.5s ease-in-out;
}

.btn.btn-pink:hover {
  background: transparent;
  border-color: #fff;
  color: #fff;
}

.btn.btn-white {
  transition: all 0.5s ease-in-out;
  color: var(--text-color);
  border-color: var(--border-color);
}

.btn.btn-white:hover {
  background: #6265fb;
  border-color: #6265fb;
  color: #fff;
}

.about-para p {
  font-weight: normal;
  color: var(--text-color);
  text-align: left;
}

.about-bullet {
  display: flex;
  justify-content: space-between;
}

.about-bullet div div {
  text-align: left;
  color: var(--text-color);
  font-size: 1.1rem;
}

.about-bullet div div span {
  color: #6265fb;
  font-weight: bold;
}

.skills-section {
  text-align: center;
  padding: 4rem 0;
  color: var(--text-color);
  display: flex;
  flex-direction: column;
  align-items: center;
}

.skills-section h1 {
  font-size: 3rem;
  margin-bottom: 1rem;
}

.skills-section p {
  font-size: 1.2rem;
  margin-bottom: 1.5rem;
}

.skills-section a {
  color: #6265fb;
  text-decoration: none;
  font-weight: bold;
}

.skills-icons {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1.5rem;
}
.skills-icons div{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.icon {
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 100px;
  margin: 10px 30px;
}

.icon img {
  width: 70px;
  height: 70px;
  margin-bottom: 0.5rem;
  transition: transform 0.3s ease;
}

.icon img:hover {
  transform: scale(1.1);
}

.icon p {
  margin: 0;
  font-size: 1rem;
  color: var(--text-color);
}




@media only screen and (max-width:900px) {
  .body {
    padding: 0px 1rem;
  }

  .header {
    padding: 0;
  }

  .intro-text {
    width: 100%;
  }
  
  .intro-section {
    flex-direction: column;
    gap: 40px;
    padding: 4rem 0rem;
  }

  .intro-image .image {
    width: 100%;
  }

  .social-icons img {
    width: 50px;
    height: 50px;
    transition: transform 0.3s ease;
  }

  .about-section {
    display: flex;
    flex-direction: column;
    gap: 40px;
  }
  .about-left, .about-right{
    width: 100%;
  }
  .about-right{
    padding: 0;
  }
  .icon {
    margin: 10px 20px;
  }
  .about-para p{text-align: center;}
}

@media screen and (min-width: 901px) {
  .about-section {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
  .about-left{
    width: 40%;
  }
   .about-right{
    width: 50%;
    padding: 0 3rem;
  }
  .skills-section p{
    width: 80%;
  }
}

@media screen and (max-width: 500px) {
.about-bullet{
  flex-direction: column;
}
.intro-text h1{
  font-size: 30px;
}
.about-right h1{font-size: 50px;}
}