h1{
    text-align: center;
  }
  .gallery {
    padding: 1.5rem 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    gap: 20px;
  }
  
  .gallery-item {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
  }

@media only screen and (max-width:900px) {
  
    .gallery-item{
      flex-direction: column;
      align-content: center;
    }
    
  }